import React, { useState, useEffect } from 'react';
import Navbar from './Components/Navbar';
import Contents from './Components/Contents';
import './App.css';

const fetchProductUrl = 'https://www.mocky.io/v2/5c9105cb330000112b649af8';

// Search Product by Search Key
const searchProduct = (searchFilterKey = '', products) => {
  const filterProduct = products.filter((product) => product.name.toLowerCase().includes(searchFilterKey.toLowerCase()));
  return filterProduct;
};

// Filter Product by Searched Product & it's filter style choices
const filterProductStyle = (filterStyles, products) => {
  let filterProduct = [];

  products.map((product) => {
    const { name, furniture_style } = product;

    furniture_style.map((style) => {
      if (filterStyles.includes(style) && !filterProduct.find((filterPrd) => filterPrd.name === name )) {
        filterProduct.push(product);
      }
    })
  })

  return filterProduct;
}

const App = () => {
  const [products, setProducts] = useState([]);
  const [furniture_styles, setFurnitureStyles] = useState([]);
  const [searchFilterKey, setSearchFilterKey] = useState('');
  const [filterStyles, setFilterStyles] = useState([])
  const [filterTimeDelivery, setFilterTimeDelivery] = useState([])
  const [productsFiltered, setProductFilter] = useState([]);

  // TODO: ONLY FETCH ON FIRST RELOAD
  useEffect(() => {
    fetchProducts();
  }, []);

  // Search Product by Search Key
  useEffect(() => {
    console.log('--> 1st useEffect Triggered!')
    setProductFilter(searchProduct(searchFilterKey, products));
  }, [searchFilterKey]);
  
  
  // Filter Product by Searched Product & it's filter style choices
  useEffect(() => {
    const searchedProducts = searchProduct(searchFilterKey, products);
    
    if (!filterStyles.length) {
      setProductFilter(searchedProducts);
    } else {
      setProductFilter(filterProductStyle(filterStyles, searchedProducts));
    }
  }, [filterStyles]);

  const fetchProducts = async () => {
    const fetchingResponse = await fetch(fetchProductUrl);
    const response = await fetchingResponse.json();
    const { products, furniture_styles } = response;

    if (furniture_styles.length) {
      setFurnitureStyles(furniture_styles);
    }

    if (products.length) {
      setProducts(products);
    }
  }

  return (
    <div className="root">
      <Navbar 
        furniture_styles={furniture_styles}
        filterStyles={filterStyles}
        onFilterKeyChanges={value => setSearchFilterKey(value)}
        onStylesChanges={value => setFilterStyles(value)}
      />
      <Contents 
        products={(productsFiltered.length) ? (productsFiltered) : (products)}
      />
    </div>
  )
}

export default App;
